.contact-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 300px;
    height: 350px;
    background-color: white;
    margin: 25px;
    transition: transform .5s;

    &:hover {
        transform: scale(1.1);
    }
    

    .contact-image {
        // position: absolute;
        // top: 10%;
        // display: grid;
        text-align: -webkit-center;
        width: 300px;
    }

    .contact-details { 
        // position: absolute;
        // top: 40%;
        display: grid;
        text-align: -webkit-center;
        width: 100%;

        .single-detail {
            padding: 3px;
            color: #af0522;
        }
    }
}